        name: 'PocketMine-MP_v3.10.1',
        description: 'This build provides support for Minecraft Bedrock Edition v1.13 that was released a few days ago.',
        verified: true,
        author: {
          name: 'JackMD',
          website: 'https://jacktaylor.cc'
        },
        downloads: {
          phar: '/static/PocketMine-MP_3.10.1.phar',
          zip: '/static/PocketMine-MP_3.10.1.zip'
        }
